
export type Language = 'fr' | 'kr' | 'en';

export type WordCategory = string;

export interface Word {
  id: string;
  text: string;
  language: Language;
  category: WordCategory;
}

export interface TranslationGroup {
  id: string;
  category: WordCategory;
  translations: Record<Language, string>;
}

